import clsx from 'clsx'
import type { PropsWithChildren } from 'react'
import React from 'react'

type GroupDirection = 'row' | 'col'

export interface CheckboxGroupProps {
  /**
   * The layout direction of the checkbox group.
   * Use `row` for horizontal, `col` for vertical
   * @default 'col'
   */
  direction?: GroupDirection
  /**
   * Whether the checkbox group is in an error state
   * @default false
   * */
  error?: boolean
  /**
   * Additional descriptive text or error text to display below the checkbox group
   * @default ''
   */
  helperText?: string
}

function Group({
  children,
  error = false,
  helperText = '',
  direction = 'col',
}: PropsWithChildren<CheckboxGroupProps>): JSX.Element {
  return (
    <div>
      <div
        className={clsx(
          `flex flex-${direction}`,
          direction === 'row' ? 'items-center' : '',
        )}
        role="group"
      >
        {children}
      </div>
      {helperText ? (
        <span
          aria-live={error ? 'assertive' : 'off'}
          className={clsx(
            'text-xs',
            error ? 'text-error-main' : 'text-black-60',
          )}
        >
          {helperText}
        </span>
      ) : null}
    </div>
  )
}

export default Group
